import React from "react";

import { FaFacebookF } from "@react-icons/all-files/fa/FaFacebookF";
import { FaTwitter } from "@react-icons/all-files/fa/FaTwitter";
import { FaGooglePlusG } from "@react-icons/all-files/fa/FaGooglePlusG";
import { FaPinterestP } from "@react-icons/all-files/fa/FaPinterestP";
import { FaLinkedinIn } from "@react-icons/all-files/fa/FaLinkedinIn";

const ShareButtons = ({ url, title }: { url: string; title: string }) => {
  return (
    <>
      <ShareButton
        media="Facebook"
        url={`https://www.facebook.com/sharer/sharer.php?u=${url}&p[title]=${title}`}
      >
        <FaFacebookF size="20px" />
      </ShareButton>
      <ShareButton
        media="Twitter"
        url={`http://twitter.com/share?text=${title}&url=${url}`}
      >
        <FaTwitter size="20px" />
      </ShareButton>
      <ShareButton
        media="Google+"
        url={`https://plus.google.com/share?url=${url}`}
      >
        <FaGooglePlusG size="20px" />
      </ShareButton>
      <ShareButton
        media="Pinterest"
        url={`http://pinterest.com/pin/create/button/?url=${url}&description=${title}`}
      >
        <FaPinterestP size="20px" />
      </ShareButton>
      <ShareButton
        media="Linkedin"
        url={`http://www.linkedin.com/shareArticle?mini=true&url=${url}&title=${title}&source=CodeCave`}
      >
        <FaLinkedinIn size="20px" />
      </ShareButton>
    </>
  );
};

export const ShareButton = ({
  url,
  media,
  children,
}: React.PropsWithChildren<{ url: string; media: string }>) => {
  return (
    <a href={url} target="_blank" title={`Share on ${media}`}>
      <button
        aria-label={`Share on ${media}`}
        className="w-12 h-12 border border-solid rounded-full border-main-10 flex justify-center items-center m-2 hover:bg-secondary-70"
      >
        {children}
      </button>
    </a>
  );
};

export default ShareButtons;
